import React, { PropsWithChildren } from 'react'
import Television from './television'
import styled from 'styled-components'


const Container = styled.div`
  position: fixed;
  bottom: 0;
  right: 5rem;
  width: 50vw;
  height: 100vh;
  min-width: 850px;
  z-index: 3;
  overflow-x: hidden;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  filter: contrast(170%) brightness(90%);
  pointer-events: none;

  @media (max-width: 740px) {
      right: 0;
      min-width: 100%;
  }
`
const Hugo: React.FC<PropsWithChildren<{}>> = ({ children }) => {
  return (
    <Container>
      <Television>
        {children}
      </Television>
    </Container>
  )
}

export default Hugo
