import React from "react";
import { Link, useLocation } from "react-router-dom";
import { Navigation as NavigationStyled, NavigationElement } from "../ui";
import styled from "styled-components";

const Container = styled.div`
    position: relative;
    z-index: 6;
    padding: 5rem 2rem 5rem 5rem;
    box-sizing: border-box;

    @media (max-width: 1260px) {
        margin-right: 0rem;
        width: 100%;
        padding: 4rem 4rem 1rem;
    }

    @media (max-width: 920px) {
        padding: 3rem 3rem 1rem;
    }

    @media (max-width: 740px) {
        padding: 2rem 2rem  1rem;
    }
  
    @media (max-width: 390px) {
        padding: 2rem 2rem 0rem;
    }
`

const Navigation: React.FC = () => {
    const location = useLocation()

    return (
        <Container>
            <NavigationStyled>
                <NavigationElement>
                    <h2>{'>'}Alexander<br />Mhrenberg</h2>
                    <h3>Software Engineer</h3>
                </NavigationElement>
                <NavigationElement active={location.pathname === '/'}>
                    <Link to="/">Home</Link>
                </NavigationElement>
                <NavigationElement active={location.pathname === '/cv'}>
                    <Link to="/cv">/Cv</Link>
                </NavigationElement>
                <NavigationElement active={location.pathname === '/contact'}>
                    <Link to="/contact">/Contact</Link>
                </NavigationElement>
            </NavigationStyled>
            <NavigationStyled>
                <NavigationElement active={location.pathname === '/_imprint'}>
                    <Link to="/_imprint">/_Imprint</Link>
                </NavigationElement>
            </NavigationStyled>
        </Container>
    )
}

export default Navigation